import {
	hostUrl
} from '@/http/request.js';
import {mapActions} from 'vuex';
export default {
	props:{
		gooListArr:{
			type:Array,
			default:() => {
				return []
			}
		}
	},
	data(){
		return {
			shopCartImg:'/static/images/goodListImg/shopcart.png',
			hostUrl:hostUrl
		}
	},
		
	methods:{
			...mapActions(['getAddCart']),
		goDetail(id){
			uni.navigateTo({
				url:`/pages/details/details?id=${id}`
			})
		},
		addCart(id){
			// 验证登陆信息
			let userId = uni.getStorageSync('userId');
			if(!userId){
				uni.showToast({
					title:'请先登陆',
					icon:'none'
				})
				return 
			}
		
		
			let {gooListArr} = this;
				
			let info = gooListArr.find(item =>{
				return item.id === id;
			})
			
			
			let seleteNumber = 1;
			let seleteUnitIndex = 0;
			if(seleteNumber > info.stock){
				uni.showToast({
					title:'库存不足',
					icon:'none'
				})
				return
			}
			// 加入购物车
			let option = {
				id:info.id,
				userId:userId,
				quantity:seleteNumber,
				isChecked:1,
				unit:info.unit.split('/')[seleteUnitIndex]
			}
			this.getAddCart(option);
		}
	},
	
	
}
